.App {
  display: flex;
  justify-content: center;
  background: rgb(228, 228, 228);
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

input {
  font-size: 22px !important;
}

h1 {
  text-align: center;
  color: white;
  font-size: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1 span {
  margin: 0 10px;
}

h1 svg {
  background: white;
  color: rgb(48, 110, 255);
  border-radius: 50%;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 4px solid rgb(255, 254, 254);
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.338),
    2px 6px 40px rgba(0, 0, 0, 0.168);
}

.customer-container {
  display: flex;
  flex-direction: column;
  flex: 3;
  padding: 15px;
  overflow-y: scroll;
  max-height: 100vh;
}

.customer {
  margin: 15px;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.338),
    2px 6px 40px rgba(0, 0, 0, 0.168);
  padding: 15px;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 3px;
}

details summary {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 190px;
  padding: 10px;
  cursor: pointer;
}

.customer > div {
  width: 100%;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.customer > div > div:not(:last-child) {
  flex: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.customer > div > div > div:first-child span {
  font-size: 52px;
}

.customer details {
  display: flex;
  width: 100%;
}

details > div {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.customer details > div > div {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.customer span.icon {
  font-size: 50px;
  height: 70px;
  width: 70px;
  margin: 5px;
  background: rgb(48, 110, 255);
  color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid rgb(255, 254, 254);
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.338),
    2px 6px 40px rgba(0, 0, 0, 0.168);
}

details button {
  align-self: flex-end;
}

.data {
  font-weight: 600;
}

.customer span:not(.data, .icon) {
  color: rgb(44, 44, 44);
}

.customer span {
  font-size: 22px;
}

.customer button {
  height: 35px;
  max-width: 100px;
  background: rgb(255, 39, 39);
  border: solid white 3px;
  border-radius: 3px;
  font-size: 16px;
  font-weight: 600;
  margin: 10px;
  color: white;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.338),
    2px 6px 30px rgba(0, 0, 0, 0.168);
}

.customer details input {
  width: 300px;
  height: 35px;
  padding-left: 5px;
  font-size: 16px;
  border: 2px solid rgb(53, 53, 53);
  border-radius: 3px;
  box-shadow: inset 1px 3px 5px rgba(0, 0, 0, 0.418);
}

.customer .update {
  background: rgb(6, 172, 0);
  margin-top: 0;
  padding: 5px 10px;
}

.customer-form {
  background: rgb(48, 110, 255);
  padding: 15px;
  flex: 1;
  border-right: solid white 3px;
  box-shadow: -1px 3px 5px rgba(0, 0, 0, 0.338),
    -2px 6px 40px rgba(0, 0, 0, 0.168);
  padding-top: 30px;
}

.customer details label {
  color: rgb(44, 44, 44);
}

.customer-form form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.customer-form form > div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 300px;
  margin: 10px;
}

label {
  font-weight: 600;
  margin-bottom: 5px;
  font-size: 18px;
  color: white;
  align-self: flex-start;
}

.customer-form input {
  width: 300px;
  height: 35px;
  font-size: 16px;
  border: 2px solid white;
  border-radius: 3px;
  background: rgb(224, 224, 224);
  box-shadow: inset 1px 3px 5px rgba(0, 0, 0, 0.418);
  padding-left: 5px;
}

form button {
  height: 35px;
  width: 100px;
  background: rgb(6, 172, 0);
  border: solid white 3px;
  border-radius: 3px;
  font-size: 16px;
  font-weight: 600;
  margin: 10px;
  color: white;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.338),
    2px 6px 40px rgba(0, 0, 0, 0.168);
  cursor: pointer;
}
